$('.js-show-menu-btn')
	.each((index, component) => {
		let $component = $(component);
		let $body = $(document.body);
		let $presentationLead = $('.js-presentation-lead');

		function checkScrollTop() {
			if ($('.js-scroller')
				.scrollTop() < $presentationLead.outerHeight() - 100) {
				$component.addClass('show-menu__btn_active');
			} else {
				$component.removeClass('show-menu__btn_active');
			}
		}

		if ($presentationLead && $presentationLead.hasClass('presentation-lead_has-bg')) {
			checkScrollTop();

			$('.js-scroller')
				.on('ps-scroll-y', () => {
					checkScrollTop();
				});
		}

		$component.on('click', (e) => {
			e.preventDefault();

			$body.trigger('main-menu:open');
		});
	});
