$('.js-standstill')
	.each((index, component) => {
		let $component = $(component);
		let $counter = $component.find('.js-counter');
		let animationInterval = null;
		let standstillTimeout = null;

		function showStandStillTimeout() {
			standstillTimeout = setTimeout(() => {
				$component.fadeIn(300);
				$component.trigger('timeout:start');
			}, 420000);
		}

		function thirtySecondsTimeout(counter) {
			if (Number(counter) === 0) {
				window.location = '/';
				return 0;
			} else {
				return Number(--counter);
			}
		}

		showStandStillTimeout();

		$component.on('timeout:start', () => {
			$counter.html(30);

			animationInterval = setInterval(() => {
				let counter = thirtySecondsTimeout($counter.html());

				$counter.html(counter);
			}, 1000);
		});

		$component.on('timeout:stop', () => {
			if (standstillTimeout) {
				clearTimeout(standstillTimeout);
				standstillTimeout = null;
				showStandStillTimeout();
			}

			if (animationInterval) {
				clearInterval(animationInterval);
				animationInterval = null;
			}
		});

		// clear timeouts on body click
		$(document.body)
			.on('click', () => {
				$component.trigger('timeout:stop');
			});

		// hide component and clear timeouts on component click
		$component.on('click', () => {
			$component.stop(true, true)
				.fadeOut(300);

			$component.trigger('timeout:stop');
		});
	});
