import PerfectScrollbar from 'perfect-scrollbar';

$('.js-instruction-popup').each((index, component) => {
	let $component = $(component);
	let $content = $component.find('.js-instruction-popup-content');
	let $closeBtn = $component.find('.js-instruction-popup-close');

	const ps = new PerfectScrollbar($content[0], {
		suppressScrollX: true,
	});

	ps.update();

	$(document.body).on('instruction:show', () => {
		$component.fadeIn(200);

		ps.update();
	});

	$(document.body).on('instruction:hide', () => {
		$component.fadeOut(200);
	});

	$closeBtn.on('click', (e) => {
		e.preventDefault();

		$(document.body).trigger('instruction:hide');
	});
});
