import PerfectScrollbar from 'perfect-scrollbar';

$('.js-scroller')
	.each((index, component) => {
		let $component = $(component);
		let toTopBtn = $('.js-to-top-btn');

		const ps = new PerfectScrollbar($(component)[0], {
			suppressScrollX: true,
		});

		$component.on('ps-scroll-y', () => {
			if ($component.scrollTop() >= $(window).innerHeight()) {
				toTopBtn.addClass('to-top-btn_active');
			} else {
				toTopBtn.removeClass('to-top-btn_active');
			}
		});

		toTopBtn.on('click', (e) => {
			e.preventDefault();

			$component.animate({scrollTop: 0}, 500);
		});

		$(document.body)
			.on('scroll:update', () => {
				ps.update();
			});
	});
